import memoizeOne from 'memoize-one';
import { sendNotification } from './utils';

export const getConstraints = memoizeOne((audioInput = null, videoInput = null) => {
  const constraints = {
    audio: {
      echoCancellation: true,
      autoGainControl: true,
      noiseSuppression: true,
    },
    video: {
      width: 440,
      height: 250,
    },
  };

  if (!audioInput?.deviceId) {
    constraints.audio = false;
  } else {
    constraints.audio.deviceId = audioInput.deviceId;
  }

  if (!videoInput?.deviceId) {
    constraints.video = false;
  } else {
    constraints.video.deviceId = videoInput.deviceId;
  }

  if (constraints.video === false && constraints.audio === false) {
    return null;
  }
  return constraints;
});

export async function getStream(constraints) {
  try {
    navigator.getUserMedia =
      navigator.getUserMedia ||
      navigator.webkitGetUserMedia ||
      navigator.mozGetUserMedia ||
      navigator.msGetUserMedia;
    if (navigator.getUserMedia || navigator.mediaDevices.getUserMedia) {
      const stream = (await navigator.mediaDevices.getUserMedia)
        ? navigator.mediaDevices.getUserMedia(constraints)
        : await navigator.getUserMedia(constraints);
      return stream;
    }
  } catch (e) {
    console.log('ERROR', e);
    let message;
    switch (e.name) {
      case 'NotFoundError':
      case 'DevicesNotFoundError':
        message = 'Please setup your webcam first.';
        break;
      case 'SourceUnavailableError':
        message = 'Your webcam is busy';
        break;
      default:
        message = 'Permission denied!';
        break;
    }
    sendNotification(message, { type: 'error' });
  }
  return null;
}

export async function getUserMedia(constraints) {
  try {
    const stream = await getStream(constraints);
    if (stream) {
      stream.getTracks().forEach((track) => {
        track.stop();
      });
    }
  } catch (e) {
    console.error('ERROR', e.message);
  }
}

function mapDevice(d) {
  return {
    deviceId: d?.deviceId,
    label: d?.label,
    kind: d?.kind,
    groupId: d?.groupId,
  };
}

export async function getAvailableDevices() {
  console.log('getAvailableDevices');
  const audioInputDevices = [];
  const audioOutputDevices = [];
  const videoDevices = [];
  const deviceErrors = [];
  try {
    const devices = await navigator.mediaDevices.enumerateDevices();
    devices.forEach((d) => {
      if (!d?.deviceId || !d?.label) {
        return;
      }
      if (d.kind === 'audioinput') {
        audioInputDevices.push(mapDevice(d));
      } else if (d.kind === 'audiooutput') {
        audioOutputDevices.push(mapDevice(d));
      } else if (d.kind === 'videoinput') {
        videoDevices.push(mapDevice(d));
      }
    });
    if (audioInputDevices.length === 0) {
      deviceErrors.push('audio');
    }
    if (videoDevices.length === 0) {
      deviceErrors.push('video');
    }
    if (audioOutputDevices.length === 0) {
      audioOutputDevices.push({
        kind: 'audiooutput',
        deviceId: 'default',
        label: 'Default speaker',
      });
    }
  } catch (e) {
    sendNotification(e.message, { type: 'error' });
  }
  audioInputDevices.push({
    kind: 'audioinput',
    deviceId: null,
    label: 'Microphone off',
  });
  audioOutputDevices.push({
    kind: 'audiooutput',
    deviceId: null,
    label: 'Speaker off',
  });
  videoDevices.push({
    kind: 'videoinput',
    deviceId: null,
    label: 'Camera off',
  });
  return { audioInputDevices, audioOutputDevices, videoDevices, deviceErrors };
}

export async function checkCameraMicPermission() {
  try {
    if (!navigator.permissions) {
      const { deviceErrors } = await getAvailableDevices();
      console.log('deviceErrors', deviceErrors);
      return deviceErrors.length === 0;
    }
    const [cameraPermission, micPermission] = await Promise.all([
      navigator.permissions.query({ name: 'camera' }),
      navigator.permissions.query({ name: 'microphone' }),
    ]);
    return cameraPermission?.state === 'granted' && micPermission?.state === 'granted';
  } catch (e) {
    console.error(e.message);
    return false;
  }
}

export async function requestCameraMicPermissions() {
  let granted = await checkCameraMicPermission();
  if (!granted) {
    await getUserMedia({
      audio: true,
      video: true,
    });
    granted = await checkCameraMicPermission();
  }
  return granted;
}

export async function getDefaultDevice(type) {
  console.log('getDefaultDevice');
  // await getUserMedia(getConstraints());
  const devices = await navigator.mediaDevices.enumerateDevices();
  if (type === 'audio') {
    const audioInputDevices = devices.filter((d) => d.kind === 'audioinput');
    if (audioInputDevices.length === 0) {
      return null;
    }
    let audioInput = audioInputDevices.find((d) => d.deviceId === 'default');
    if (audioInput === undefined) {
      audioInput = audioInputDevices[0];
    }
    return mapDevice(audioInput);
  }
  if (type === 'video') {
    const videoDevices = devices.filter((d) => d.kind === 'videoinput');
    if (videoDevices.length === 0) {
      return null;
    }
    let videoInput = videoDevices.find((d) => d.deviceId === 'default');
    if (videoInput === undefined) {
      videoInput = videoDevices[0];
    }
    return mapDevice(videoInput);
  }
  return null;
}
