import React, { PureComponent } from 'react';
import { requestCameraMicPermissions, getConstraints, getStream, getDefaultDevice } from './userMediaUtils';
import './App.css';

class App extends PureComponent {
  constructor(props) {
    super(props);
    this.videoRef = React.createRef();
  }

  async componentDidMount() {
    await requestCameraMicPermissions();
    const videoInput = await getDefaultDevice('video');
    console.log('videoInput', videoInput);
    const constraints = getConstraints(null, videoInput);
    if (constraints) {
      const stream = await getStream(constraints);
      this.videoRef.current.src = '';
      this.videoRef.current.srcObject = stream;
    }

  }

  componentWillUnmount() {
    console.log('componentWillUnmount JoinMeetingDialog');
    if (this.videoRef.current) {
      this.videoRef.current.src = '';
      this.videoRef.current.srcObject = null;
    }
  }

  render() {
    return (
      <div className="App">
        <video muted ref={this.videoRef} id="video" autoPlay></video>
      </div>
    );
  }
}

export default App;
