export function sendNotification(message, options = {}) {
  // return { type: types.SEND_NOTIFICATION, data: notification };
  let { type, duration } = options;
  if (type === undefined) {
    type = 'success';
  }
  if (duration === undefined) {
    duration = 4000;
  }
  const detail = {
    message,
    options: {
      type,
      duration,
    },
  };
  document.dispatchEvent(new CustomEvent('ADD_NOTIFICATION_MESSAGE', { detail }));
}
